.row_box {
  background: #fff;
  margin-bottom: 10px;
  padding: 10px 0;
  position: relative;
  color: #888; }
  .row_box > span {
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px;
    font-size: 13px; }

.goods_box {
  font-size: 12px;
  padding-bottom: 0;
  margin-top: 10px; }
  .goods_box .goods_info {
    border-bottom: 1px solid #e5e5e5;
    padding: 0 65px 15px 75px; }
    .goods_box .goods_info .goods_img {
      position: absolute;
      top: 10px;
      left: 10px;
      height: 60px;
      width: 60px;
      border: 1px solid #e5e5e5; }
      .goods_box .goods_info .goods_img img {
        width: 100%; }
    .goods_box .goods_info .goods_name p {
      line-height: 20px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis; }
    .goods_box .goods_info .goods_price {
      position: absolute;
      right: 10px;
      top: 10px;
      height: 60px;
      line-height: 60px;
      color: #ff5555; }
  .goods_box .goods_sum {
    padding: 20px 10px;
    overflow: hidden;
    font-size: 14px;
    line-height: 28px; }
    .goods_box .goods_sum .goods_sum_select {
      float: right; }
      .goods_box .goods_sum .goods_sum_select span {
        width: 30px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        display: inline-block;
        border: 1px solid #05a8f9;
        border-radius: 3px;
        color: #05a8f9;
        vertical-align: middle; }
        .goods_box .goods_sum .goods_sum_select span.disabled {
          border-color: #e5e5e5;
          color: #e5e5e5; }
      .goods_box .goods_sum .goods_sum_select input {
        width: 75px;
        height: 30px;
        line-height: 30px;
        padding: 0 5px;
        display: inline-block;
        border: 1px solid #e5e5e5;
        border-radius: 3px;
        vertical-align: middle;
        text-align: center; }
  .goods_box .goods_count {
    background: #f1f1f1;
    padding: 15px 10px;
    text-align: right;
    font-size: 12px; }
    .goods_box .goods_count big {
      font-size: 16px;
      color: #ff5555; }

.pay_methods {
  padding: 40px 10px 10px; }
  .pay_methods ul {
    border-top: 1px solid #e5e5e5;
    overflow: hidden;
    padding-top: 5px; }
    .pay_methods ul li {
      float: left;
      padding: 5px 5px 5px 35px;
      margin-right: 5px;
      border: 1px solid #e5e5e5; }
      .pay_methods ul li.wachat_pay {
        background: url("../../../images/wachat_03.png") no-repeat 5px center;
        background-size: auto 80%; }
      .pay_methods ul li.on {
        position: relative;
        border-color: #ff5555; }
        .pay_methods ul li.on i {
          position: absolute;
          right: -7px;
          bottom: -2px;
          z-index: 9;
          height: 10px;
          width: 10px;
          border-top: 10px solid #ff5555;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          transform: rotate(-45deg); }
        .pay_methods ul li.on i:before,
        .pay_methods ul li.on i:after {
          content: ' ';
          position: absolute;
          z-index: 10;
          height: 2px;
          display: block;
          background: #fff;
          font-size: 12px; }
        .pay_methods ul li.on i:before {
          left: -4px;
          top: -7px;
          width: 5px;
          transform: rotate(90deg); }
        .pay_methods ul li.on i:after {
          left: -3px;
          top: -6px;
          width: 7px; }

.load_text {
  background: #fff;
  text-align: center;
  padding: 10px 0; }

.order_message {
  padding: 40px 10px 10px; }
  .order_message .message_box {
    border-top: 1px solid #e5e5e5;
    padding-top: 5px; }
    .order_message .message_box textarea {
      width: 100%;
      border: 1px solid #e5e5e5;
      border-radius: 3px;
      padding: 10px;
      font-size: 12px; }
  .order_message .btn_box {
    padding-top: 10px; }
    .order_message .btn_box a {
      width: 100%;
      text-align: center;
      display: block;
      padding: 10px;
      background: #ff5555;
      color: #fff;
      border: 0;
      border-radius: 3px; }
